import React from "react";

function Chapter(props) {
    const {books, book, chapter, verses} = props;

    return(
        <div className="chapter">
            <h3>{books.filter(item => (item.eng_short === book))[0].name} {chapter}.</h3>
            <hr></hr>
            {verses.map(verse => (
            verse.verse_no === 1 ?
                <span key={verse._id}>
                {/* <b className="chapter-number">{chapter}</b> */}
                    &nbsp; &nbsp;
                    <sup>
                        {verse.verse_no}
                    </sup>
                    {verse.verse_text}
                </span> :
                <span key={verse._id}>
                    &nbsp;
                    <sup>
                        {verse.verse_no}
                    </sup>
                    {verse.verse_text}
                </span>
                )
            )}
        </div>
    );
}

export default Chapter;